@import "./Modules/sass/style";
@import "./config/fingo-colors";

// ----------------------------------------------------
// variables
$bg-color: #fff;
$fg-color: #2b2b2b;
$carousel-tab: #999999;

// ----------------------------------------------------
// webfonts
// @import fonts

// ----------------------------------------------------
// common module
%focus-state {
  outline: none;
  outline-offset: 10px;
  box-shadow: 0 0 0 4px rgba($fingo-mian-color, 0.5);
  border-radius: 2px; }

%reset-list {
  list-style: none;
  margin: {
    top: 0;
    bottom: 0; }
  padding-left: 0; }

%reset-button {
  border: none;
  padding: 0;
  background: transparent; }

.readable-hidden {
  overflow: hidden;
  position: absolute;
  clip:     rect(0,0,0,0);
  width:    1px;
  height:   1px;
  margin:   -1px;
  border:   0;
  padding:  0;
  &.focusable:focus {
    overflow: visible;
    position: static;
    clip:     auto;
    width:    auto;
    height:   auto;
    margin:   0; } }

@mixin link($color) {
  color: $color;
  text-decoration: none; }

@mixin circle($size: 10px, $color: currentColor) {
  display: block;
  width: $size;
  height: $size;
  border-radius: 50%;
  background: $color; }

