// ----------------------------------------------------
// ui-carousel
.ui-carousel {
  @include size(100%);
  position: relative;
  margin: {
   left: auto;
   right: auto; } }

.ui-carousel-mask {
  @include size(100%);
  overflow: hidden;
  position: relative;
  // flex: 0 1 100%
  // width: 1200px
  // height: 640px
  // background: $bg-color
  // box-shadow: 0 0 10px hsla(0,0%,0%,.15)
  // border: 1px solid rgba($fg-color, 0.2)
  margin: {
   left: auto;
   right: auto; } }

.ui-carousel-headline {
  // @extend .readable-hidden
  position: absolute;
  z-index: 1000;
  left: 30px;
  top: 30px;
  width: 10em;
  margin: 0;
  font: {
    size: 14px; }
  line-height: 1.34;
  color: #ac807a; }

.ui-carousel-tablist {
  position: absolute;
  z-index: 100000;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  ul {
    @extend %reset-list;
    display: block;
    text-align: center; }
  li {
    display: inline-block;
    margin: {
      left: 5px;
      right: 5px; }
    &.active .ui-carousel-tab {
      position: relative;
      top: 1px;
      background: #fff;
      border: 1px solid #fff;
      @include size(25px 10px);
      border-radius: 10px; } }
  .ui-carousel-tab {
    @include link($carousel-tab);
    @include circle();
    &:focus {
      @extend %focus-state;
      border-radius: 50%; }
    &:hover {
      color: $fingo-mian-color; } } }

.ui-carousel-button-group {
  button {
    @extend %reset-button;
    @include size(45px);
    border-radius: 50%;
    cursor: pointer;
    opacity: 1;
    position: absolute;
    z-index: 100000;
    top: 50%;
    transform: translateY(-50%);
    &:hover {
      opacity: 1; }
    &:focus {
      @extend %focus-state; }
    &::before {
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      transition: all 0.2s ease-in-out; } }
  .ui-carousel-prev-button {
    left: -55px;
    &::before {
      top: 50%;
      left: calc(50% + 5px);
      transform: translate(-50%, -50%) rotate(-45deg);
      border: {
        top: 3px solid #8b8b8b;
        left: 3px solid #8b8b8b; } }
    &:hover::before {
      border: {
        top: 3px solid #fff;
        left: 3px solid #fff; } } }
  .ui-carousel-next-button {
    right: -55px;
    &::before {
      top: 50%;
      left: calc(50% - 5px);
      transform: translate(-50%, -50%) rotate(45deg);
      border: {
        top: 3px solid #8b8b8b;
        right: 3px solid #8b8b8b; } }
    &:hover::before {
      border: {
        top: 3px solid #fff;
        right: 3px solid #fff; } } } }

.ui-carousel-tabpanel-wrapper {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }


.ui-carousel-tabpanel {
  margin: 0;
  float: left;
  @include size(100%);
  img {
    // margin-top: 30px
    width: 100%;
    height: auto; }
